import React from "react"
import Heading from "../../common/Heading"
import PropertiesMap from "./PropertiesMap"
import "./style.css"

const Location = ({ properties = [] }) => {
  return (
    <>
      <section className='location padding'>
        <div className='container'>
          <Heading title='Explore por Regio' subtitle='Encontre as melhores oportunidades de investimento em diferentes regies.' />

          {properties.length > 0 && (
            <div className='map-section mtop'>
              <h3>Mapa de Imveis</h3>
              <PropertiesMap properties={properties} />
            </div>
          )}
        {properties.length === 0 && <p className='empty-message'>Nenhum imvel encontrado para exibir no mapa.</p>}

        </div>
      </section>
    </>
  )
}

export default Location