import Papa from "papaparse"

// Funo para carregar dados do CSV
export const loadListFromCSV = async () => {
  try {
    const response = await fetch(`${process.env.PUBLIC_URL}/data/imoveis.csv`)
    const csvText = await response.text()
    
    return new Promise((resolve, reject) => {
      Papa.parse(csvText, {
        header: true,
        skipEmptyLines: true,
        complete: (results) => {
          const data = results.data.map((row) => ({
            id: parseInt(row.id),
            cover: `${process.env.PUBLIC_URL}${row.cover}`,
            images: (row.images || row.cover)
              .split(';')
              .map(img => `${process.env.PUBLIC_URL}${img.trim()}`),
            name: row.name,
            location: row.location,
            category: row.category,
            price: row.price,
            type:  row.type,
            leilao: row.leilao,
          }))
          resolve(data)
        },
        error: (error) => {
          reject(error)
        },
        delimiter: '|', // Ajustado para usar o delimitador '|
      })
    })
  } catch (error) {
    console.error("Erro ao carregar o CSV:", error)
    throw error
  }
}

// Varivel que ser preenchida com dados do CSV
export var list = []

// Carregar dados do CSV ao importar este mdulo
loadListFromCSV().then((data) => {
  console.log("Dados carregados do CSV:", data) // Verificar os dados carregados
  list = data
})

